export default function any({ resetLink }: { resetLink: string }) {
  return (
    <div style={{ fontFamily: "Arial, sans-serif", margin: 0, padding: 0 }}>
      <table
        width="100%"
        cellPadding="0"
        cellSpacing="0"
        style={{ backgroundColor: "#1f2937 ", padding: "20px 0" }}
      >
        <tr>
          <td align="center">
            <table
              width="600"
              cellPadding="0"
              cellSpacing="0"
              style={{
                backgroundColor: "#111827",
                border: "1px solid #e0e0e0",
                borderRadius: "8px",
                overflow: "hidden",
              }}
            >
              <tr>
                <td
                  align="center"
                  style={{
                    padding: "30px 20px",
                    background:
                      "linear-gradient(to right, #312e81 , #3730a3 , #4338ca , #6366f1 , #818cf8)",
                  }}
                >
                  <h1
                    style={{
                      margin: 0,
                      fontSize: "32px",
                      fontWeight: 600,
                      color: "transparent",
                      background:
                        "linear-gradient(to right, #1f2937, #4f46e5, #6366f1, #4f46e5, #1f2937)",
                      WebkitBackgroundClip: "text",
                      backgroundClip: "text",
                    }}
                  >
                    Assetly
                  </h1>
                  <p
                    style={{
                      margin: "8px 0 0",
                      fontSize: "18px",
                      color: "#ffffff",
                    }}
                  >
                    Resetea tu contraseña
                  </p>
                </td>
              </tr>
              <tr>
                <td
                  style={{
                    padding: "30px",
                    color: "#333333",
                    fontSize: "16px",
                    lineHeight: 1.5,
                  }}
                >
                  <p>Hola,</p>
                  <p>
                    Hemos recibido una solicitud para restablecer tu contraseña.
                    Por favor, haz clic en el siguiente botón para proceder:
                  </p>
                  <p style={{ textAlign: "center", margin: "30px 0" }}>
                    <a
                      href={resetLink}
                      style={{
                        display: "inline-block",
                        padding: "12px 24px",
                        color: "#ffffff",
                        textDecoration: "none",
                        borderRadius: "5px",
                        background: "linear-gradient(to top, #4f46e5, #6366f1)",
                        fontWeight: 500,
                        transition: "all 0.2s ease",
                      }}
                    >
                      <span
                        style={{ display: "inline-flex", alignItems: "center" }}
                      >
                        Resetear Contraseña
                        <span
                          style={{
                            marginLeft: "4px",
                            transition: "transform 0.2s",
                          }}
                        >
                          →
                        </span>
                      </span>
                    </a>
                  </p>
                  <p>Este enlace expirará en 30 minutos.</p>
                  <p>Si no solicitaste el reseteo, ignora este correo.</p>
                  <p>
                    Saludos,
                    <br />
                    El equipo de Assetly
                  </p>
                </td>
              </tr>
              <tr>
                <td
                  align="center"
                  style={{
                    padding: "20px",
                    fontSize: "12px",
                    color: "#777777",
                    backgroundColor: "#f0f0f0",
                  }}
                >
                  <p style={{ margin: 0 }}>
                    Tu forma Segura y Eficiente de Gestionar tus Activos
                  </p>
                  <p style={{ margin: "8px 0 0" }}>
                    © 2025 Assetly. Todos los derechos reservados.
                  </p>
                </td>
              </tr>
            </table>
          </td>
        </tr>
      </table>
    </div>
  );
}
